import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import ListaAgenda from '../views/ListaAgenda.vue'
import ListaFielAgenda from '../views/ListaFielAgenda.vue'
import Configuracao from '../views/Configuracao.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
    
  },
  {
    path: '/listaagenda',
    name: 'ListaAgenda',
    component: ListaAgenda
  },  
  {
    path: '/config',
    name: 'Config',
    component: Configuracao
  },
  {
    path: '/listapresenca',
    name: 'ListaFielAgenda',
    component: ListaFielAgenda
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
